
.main{
      width: 1280px;
}


.header {
    border: 1px solid red;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    justify-items: center;
    padding: 20px;

}

.table {

    border: 1px solid green;
    display: flex;
    flex-flow: row;
    align-items: inherit;
    justify-content: space-between;
    flex-direction: column;

}

.row {

    display: grid;
    grid-template-columns: repeat(5, 1fr);
    justify-items: center;
    padding: 20px;
}

